/* reset */
body,
div,
header,
footer,
nav,
section,
article,
aside,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
ol,
dl,
dt,
dd,
p,
table,
th,
tr,
th,
td,
form,
fieldset,
legend,
figure {
  padding: 0;
  margin: 0;
}

ul,
ol,
li {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
fieldset,
img {
  border: 0 none;
}
img {
  width: 100%;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
input,
select,
textarea,
button {
  padding: 0;
  margin: 0;
  font-size: 100%;
  vertical-align: middle;
  border: 0 none;
}
button {
  border: 1px solid #666;
  background-color: transparent;
  cursor: pointer;
}
a,
a:hover,
a:active {
  text-decoration: none;
}
address,
em {
  font-style: normal;
}
strong {
  font-weight: normal;
}
label {
  margin: 0;
}

