/* #trade */
#trade,
#orders {
  background-color: white;
  margin: 30px 0 60px;
  border-radius: 4px;
  box-shadow: 0 10px 10px -4px #adadad75;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#trade,
#orders {
  .table_container {
    width: 100%;
    padding: 15px;
    overflow: auto;
    scroll-behavior: smooth;
    scrollbar-color: transparent;
    a {
      color: #888;
      &:hover {
        color: var(--primary-color);
      }
    }
    table {
      tbody tr{
        &:hover{
          background-color: #fefefe;
        }
      }
    }
  }
}

#trade table {
  width: 100%;
  min-width: 930px;
  overflow-x: auto;
}

#trade table thead {
  border-bottom: 1px solid #f5f5f5;
}

#trade table thead th {
  padding: 10px;
  color: #76808f;
  font-weight: 400;
}

#trade table thead th.user {
  width: 25%;
}

#trade table thead th.price {
  width: 15%;
}

#trade table thead th.order_limit {
  width: 25%;
}

#trade table thead th.payment {
  width: 20%;
}

#trade table thead th.action {
  width: 20%;
}

#trade table tbody tr {
  border-bottom: 1px solid #f5f5f5;
  &:hover{
    background-color: #fefefe;
  }
}

#trade table tbody tr td {
  padding: 16px 12px;
}

#trade,
#orders {
  table tbody .user {
    .user__card {
      color: #a5a5a5;
      font-weight: 500;
      line-height: 0;
      vertical-align: bottom;
      display: flex;
      gap: 8px;
      line-height: 1.25;
      .verify-badge {
        color: #85c4f9;
        margin-left: 5px;
      }
    }
    .username {
      color: #666;
      line-height: 1.25;
      display: inline-block;
      word-break: normal;
    }
    .user__initials {
      display: inline-block;
      width: 25px;
      height: 25px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      line-height: 1;
      border-radius: 50%;
      background-color: #dce0e9;
      color: #505050;
      opacity: 0.75;
      &.type-buy {
        background-color: var(--danger);
        color: white;
      }
      &.type-sell {
        background-color: var(--success);
        color: white;
      }
    }
  }
}

