#settings .btn-toggle {
  margin: 0 4rem;
  padding: 0;
  position: relative;
  border: none;
  height: 1.5rem;
  width: 3rem;
  border-radius: 1.5rem;
  color: #6b7381;
  background: #eaecef;
}

#settings .btn-toggle:focus,
#settings .btn-toggle.focus,
#settings .btn-toggle:focus.active,
#settings .btn-toggle.focus.active {
  outline: none;
}

#settings .btn-toggle:before,
#settings .btn-toggle:after {
  line-height: 1.5rem;
  width: 4rem;
  text-align: center;
  font-weight: 600;
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  position: absolute;
  bottom: 0;
  transition: opacity 0.25s;
}

#settings .btn-toggle > .handle {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 1.125rem;
  background: #fff;
  transition: left 0.25s;
}

#settings .btn-toggle.active {
  transition: background-color 0.25s;
}

#settings .btn-toggle.active > .handle {
  left: 1.6875rem;
  transition: left 0.25s;
}

#settings .btn-toggle.active:before {
  opacity: 0.5;
}

#settings .btn-toggle.active:after {
  opacity: 1;
}

#settings .btn-toggle.active {
  background-color: #0059ff;
}

