/*Timline*/

.my-timeline .active {
  color: #000;
}

.my-timeline {
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: step;
  color: rgb(192, 192, 192);
  display: flex;
  justify-content: center;
  width: 100%;
}

.my-timeline li {
  /* float: left; */
  text-align: center;
  /* min-width: 100px; */
  /* width: 250px; */
  flex: 1;
  position: relative;
}

.my-timeline li:before {
  content: counter(step);
  counter-increment: step;
  line-height: 35px;
  width: 35px;
  height: 35px;
  display: block;
  border-radius: 100%;
  background-color: #d6d6d6;
  margin: 0 auto 15px auto;
  color: #ffffff;
  position: relative;
  z-index: 1;
}

.my-timeline li:after {
  content: "";
  width: 75%;
  height: 1px;
  background: rgba(136, 136, 136, 0.342);
  display: block;
  position: absolute;
  color: #000000;
  top: 17.5px;
  left: 62%;
  z-index: 0;
}

.my-timeline li:last-child:after {
  content: none;
}

.my-timeline .active:before {
  background-color: #0059ff !important;
  opacity: 1;
}
.my-timeline li:first-child:after {
  background: #c4c4c4;
}

