.email-switch .switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 25px;
}

.email-switch .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.email-switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.email-switch .slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.email-switch input:checked + .slider {
  background-color: #0059ff;
}

.email-switch input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

.email-switch input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.email-switch .slider.round {
  border-radius: 34px;
}

.email-switch .slider.round:before {
  border-radius: 50%;
}

.sms-switch input:checked + .slider {
  background-color: #eaecef;
}

.otp-switch input:checked + .slider {
  background-color: #eaecef;
}

