#notification_dropdown {
  // max-width: 200px;
  .toggler {
    vertical-align: baseline;
    color: inherit;
    padding: 8px 12px;
    margin: auto;
    display: inline-block;
    border-radius: 50%;
    line-height: 1.5;
    transition: background-color 0.25s ease-in;
    position: relative;
    &:hover {
      background-color: rgba(0, 0, 0, 0.15);
    }
    span {
      margin-left: -7px;
      padding: 1px 2px;
      border: 0px solid red;
      border-radius: 14px;
      color: white;
      font-size: 10px;
      font-weight: bold;
      background-color: red;
      position: absolute;
      top: 0px;
    }
  }
  .dropdown__container {
    width: 300px;
    right: 0;
    height: auto;
    max-height: 370px;
    border-radius: 0;
    overflow-y: scroll;
    inset: 10px auto auto -10px !important;
    padding: 0;
    border: none;
    box-shadow: 8px 8px 48px -17px hsla(0, 0%, 0%, 0.6);
    .dropdown__item,
    .dropdown-item {
      white-space: normal;
      text-overflow: ellipsis;
      padding: 12px;
      background-color: white;
      &.read {
        opacity: 0.75;
        background-color: #f6f6f6;
        .item__title {
          color: #888;
        }
      }
      &:not(:last-of-type) {
        border-bottom: 1px solid #eee;
      }
      &:active {
        background-color: #ededed;
      }
      &:hover {
        .item__title {
          color: rgb(46, 162, 229);
        }
      }
      .item__title {
        font-size: 0.85rem;
        letter-spacing: -0.012em;
        font-weight: 500;
        display: block;
        line-height: 1.5;
        color: hsl(215, 57%, 30%);
      }
      .item__description {
        font-size: 0.75rem;
        line-height: 2em;
        color: rgb(108, 108, 108);
        display: block;
        text-overflow: ellipsis;
        white-space: pre-wrap;
        line-break: anywhere;
        word-break: break-word;
        max-height: 56px;
        overflow: hidden;
      }
      .item__meta {
        display: block;
        font-size: 10px;
        font-weight: 100;
        letter-spacing: 0.009em;
        line-height: 0px;
        font-style: normal;
        color: rgb(80, 80, 80);
        margin-top: 8px;
      }
    }
  }
}
.dropdown-menu.show {
