    color: var(--primary-color);
    content: "\f35d";
    font-family: "Font Awesome 5 Pro";
    font-weight: 900;
    top: -2px;
    right: -2px;
    margin-left: 8px;
    font-size: 8px;
  }
}

.transaction_block {
  display: flex;
  gap: 15px;
  padding: 20px;
  flex-wrap: wrap;
  color: var(--disabled);
  border-bottom: 1px solid #e8e8e8;
  &:hover{
    background: #fafafa;
  }
  /* 
  TRANSFER: "TRANSFER",
    WITHDRAWAL: "WITHDRAW",
    DEPOSIT: "DEPOSIT",
    CREDIT: "CREDIT",
    DEBIT: "DEBIT", */
  &.credit, &.deposit {
    color: var(--success);
  }
  &.debit, &.withdraw{
    color: var(--danger);
  }
  &.transfer{
    color: var(--primary);
  }

  .trx_symbol {
    width: 45px;
    height: 45px;
    border-radius: 45px;
    box-shadow: 0 0 0 1px currentColor;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .trx_type {
    font-weight: 300;
    font-size: 18px;
  }
  .trx_in_usd {
    font-weight: 400;
    color: inherit;
    font-size: 12px;
  }
  .trx_qty {
    font-weight: 500;
    font-size: 20px;
    color: #929292;
    line-height: 1;
    text-align: inherit;
  }

  ul.trx_meta {
    li {
      font-size: 12px;
      color: #9b9b9b;
      max-width: 150px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      &:after {
        content: "";
        width: 2px;
        height: 100%;
        background-color: #000;
      }
    }
  }
}
