@charset "utf-8";

@media screen and (max-width: 1200px) {
  /* #securityBottom */
  #securityBottom p {
    width: 70%;
  }
}

@media screen and (max-width: 992px) {
  /* #affiliateTop */
  #affiliateTop .col-lg-6 img {
    position: static;
    margin-top: 30px;
  }

  /* #dashboard */
  #dashboard .box:after {
    display: none;
  }

  #dashboard .box dl {
    width: 50%;
  }

  #dashboard .date li {
    margin-right: 10px;
  }

  /* #securityTop */
  #securityTop li dd {
    font-size: 13px;
  }

  #securityTop .recommended .btn_enable {
    top: 55px;
  }

  #securityTop .recommended .btn_unset {
    position: static;
    float: left;
    display: block;
    width: 100%;
    box-sizing: border-box;
    padding-left: 50px;
  }

  #securityTop .phone .btn_remove {
    top: 45px;
  }

  #securityTop .phone .btn_change {
    top: 45px;
  }

  #securityTop .phone p {
    position: static;
    float: left;
    display: block;
    width: 100%;
    box-sizing: border-box;
    padding-left: 50px;
  }

  #securityTop .email .btn_remove {
    top: 45px;
  }

  #securityTop .email .btn_change {
    top: 45px;
  }

  #securityTop .email p {
    position: static;
    float: left;
    display: block;
    width: 100%;
    box-sizing: border-box;
    padding-left: 50px;
  }

  /* #securityBottom */
  #securityBottom p {
    width: 100%;
  }

  #securityBottom .btn_add {
    position: static;
    margin-bottom: 30px;
  }
}

