    float: none;
    width: 100%;
    margin-top: 15px;
  }

  #dashboard .box {
    margin-top: 0;
    margin-bottom: 30px;
  }

  /* #friendsList */
  #friendsList h4 {
    font-size: 30px;
    line-height: 30px;
