    position: static;
    margin-top: 10px;
  }

  #securityTop .phone .btn_change {
    position: static;
    margin-top: 10px;
  }

  #securityTop .phone p {
    float: none;
    line-height: 30px;
  }

  #securityTop .email img {
    margin-bottom: 90px;
  }

  #securityTop .email .btn_remove {
    position: static;
    margin-top: 10px;
  }

  #securityTop .email .btn_change {
    position: static;
    margin-top: 10px;
  }

  #securityTop .email p {
    float: none;
    line-height: 30px;
  }

  #securityTop .verification img {
    margin-bottom: 80px;
  }

  #securityTop .verification .btn_enable {
    position: static;
    margin-top: 10px;
  }
}

@media screen and (max-width: 576px) {
  /* #commission */
  #commission .box i {
