}

@media screen and (max-width: 460px) {
  /* #affiliateTop */
  #affiliateTop h3 {
    font-size: 35px;
  }

  #affiliateTop p {
    font-size: 16px;
  }

  #affiliateTop .box {
    padding: 20px 15px;
  }

  #affiliateTop .box .btn_link {
    right: 15px;
    top: 20px;
  }

  #affiliateTop .box .referral_id dd {
    font-size: 25px;
  }

  /* #dashboard */
  #dashboard .dash_menu li {
    padding: 10px;
  }

  #dashboard .date li {
    margin-right: 5px;
    padding: 5px;
  }

  #dashboard .box .ranking dt a {
    margin-left: 0;
  }

  /* #commission */
  #commission h4 {
    margin-bottom: 50px;
  }

  #commission .btn_chart {
    top: 40px;
  }

  #commission .commission_menu li {
    padding: 10px 5px;
    font-size: 13px;
  }

  /* #profile */
  #profile .user_info .nickname {
    float: none;
  }

  #profile .user_info .user_id {
    float: none;
    margin: 20px 0;
  }

  #profile .bottom dl {
    width: 50%;
  }

  #profile .bottom dl:last-child {
    width: 100%;
  }
}

